@import url(https://fonts.googleapis.com/css2?family=Galada&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  background-image: url(/static/media/final_5f2acf95c3990e001356f1ed_198465.b7d30102.gif);
  background-size: cover;
}

.Auth0Button {
	border:2px solid #cea835;
	background: linear-gradient(180deg, #f7c82d 0%, #b67109 100%);
	border-radius: 75px;
	display:inline-block;
	cursor:pointer;
	color: rgb(255, 255, 161);
  font-family: 'Raleway', sans-serif;
	font-size:17px;
	font-weight:bolder;
	padding:16px 31px;
  text-shadow:2px 2px 5px #000000;
  transition: 0.1s;
}

.myButton:hover {
  background: linear-gradient( #202020 0%, #1b1b1b 50%, #000000 100%);
  border-color: #111111;
  color: #f7c82d;
}

.myButton:active {
	position:relative;
	top:1px;
}

.section {
  font-family: 'Raleway', sans-serif;
  font-weight: thin;
  text-shadow: 4px 4px 2px #000000;
  text-align: center;
  font-size: 25px;
	color: rgb(255, 255, 161);
  margin-top: 10px;
  margin-bottom: 35px;
}

.jumbo {
  background-color: rgba(15, 15, 15, 0.75);
  border-radius: 35px;
  text-align: center;
  margin-top: 25px;
  padding-top: 10px;
  padding-bottom: 15px;
}

nav.navbar {
  background: linear-gradient(180deg, #f7c82d 0%, #b67109 100%);
}

.navText {
  font-family: "Galada", cursive;
}

.navbuttons {
  color: black;
  font-weight: bolder;
}

.router-link-exact-active {
  position: relative;
  font-weight: 900;
  color: black;
  opacity: 100%;
}



.bg {
  background: black;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 1;
  overflow: auto;
}

.titleBg.card.card-body {
  background: linear-gradient(#444444 0%, #1b1b1b 50%, #000000 100%);
  color: #cea935;
  margin-top: 0;
  margin-bottom: 10px;
  padding: 1px;
  text-align: center;
  font-family: "Raleway", sans-serif;
  font-size: 20px;
  font-weight: bolder;
  line-height: normal;
}

.giglist,
.gigsetlist,
.setlistsongs,
.allsetlists,
.lyrics {
  margin: 20px;
}

.textBody,
.inputBody {
  padding: 0rem 1rem 1rem 1rem;
}

.btn-title {
  padding-left: 1rem;
}

.gigItem:hover,
.setlistItem:hover, .setlistlistItem:hover {
  color: #b67109;
  font-weight: 500;
}

.giglist.card,
.gigsetlist.card,
.setlistsongs.card,
.allsetlists.card,
.addsongs.card,
.lyrics.card {
  border-radius: 0px;
  border: solid 10px #b3b3b3;
  outline: solid 2px #e0e0e0;
}

.gigtext {
  margin-top: 40px;
}

.gigInput,
.setlistInput,
.artistInput,
.songInput {
  margin: 10px 0px 10px;
}

Button.LaunchBtn,
Button.btn {
  background: linear-gradient(180deg, #f7c82d 0%, #b67109 100%);
  color: Black;
  font-weight: 500;
}

input#songInput.form-control,
input#artistInput.form-control,
input.form-control {
  border: solid 1px black;
}

